<!--抄表报表-->
<template>
    <BoxTitle title="电量报表">
        <SearchInputs @searchHand="searchHand"></SearchInputs>
        <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="抄表报表" name="first"></el-tab-pane>
            <el-tab-pane label="月报表" name="second"></el-tab-pane>
        </el-tabs>
        <div class="down" v-show="activeName=='first'">
            <el-button type="default" @click="down">导 出</el-button>
        </div>
        <TableOne v-show="activeName=='first'" :tableData="tableData"
            :emptyText="Object.keys(search).length?'暂无数据':'请输入搜索条件查询'" :total="total">
        </TableOne>
        <TableTwo v-show="activeName=='second'" :search="search" :show="activeName=='second'" :tableData="tableDataTwo"
            :emptyText="Object.keys(search).length?'暂无数据':'请输入搜索条件查询'" :total="totalTwo">
        </TableTwo>
    </BoxTitle>
</template>
<script>
    //组件
    import BoxTitle from "@/components/BoxTitle.vue";
    import TableCommon from "@/components/TableCommon.vue";
    import TableOne from "./TableOne.vue";
    import TableTwo from "./TableTwo.vue";
    import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getReportMa,
        getReportday,
        getExportReport,
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import {
        formatNum,
    } from "@/utils/index.js";
    export default {
        extends: TableCommon, //继承通用index
        name: 'kcList',
        components: {
            BoxTitle,
            SearchInputs,
            TableOne,
            TableTwo
        },
        data() {
            return {
                activeName: 'first',
                tableDataTwo: [],
                totalTwo: 0,
                loadDataFalg: false, //默认进页面不加载
            }
        },
        mounted() {},
        methods: {
            //导出数据
            down() {
                if (this.activeName == 'first') {
                    getExportReport({
                        ...this.search,
                    }).then(tool.down)
                }
            },
            //下载文件
            download(url) {
                const fileName = this.getShowUrl(url);
                const a = document.createElement('a');
                a.setAttribute('download', fileName);
                a.setAttribute('href', resourceUrl + "?resource=" + fileName);
                a.click();
            },
            //搜索按钮
            searchHand(data) {
                this.loadDataFalg = true;
                if (data == 'reset') {
                    this.search = {};
                    this.tableData = [];
                    this.total = 0;
                } else {
                    this.search = data;
                    this._getData();
                }
            }, //操作按钮点击的事件
            //默认获取表格数据和搜索数据
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                if (!this.loadDataFalg) return;
                getReportMa({
                    ...this.search,
                }).then(res => {
                    const data = res.data ? res.data : [];
                    this.tableData = data;
                    this.total = data.length;
                })
                getReportday({
                    ...this.search,
                }).then(res => {
                    const data = res.data ? res.data : [];
                    let arr = [];
                    data.forEach((li, k) => {
                        const li2 = JSON.parse(JSON.stringify(li));
                        li.index = k + 1;
                        li.type = '电量';
                        arr.push(li)
                        li2.type = '底码';
                        arr.push(li2)
                    });
                    this.tableDataTwo = arr;
                    this.totalTwo = data.length;
                })
            },
        }
    }
</script>
<style lang="less" scoped>
    /deep/ .el-tabs__item {
        height: 40px;
        line-height: 40px;
    }

    /deep/ .el-tabs {
        margin-bottom: 16px;
    }

    .down {
        position: absolute;
        right: 24px;
        top: 180px;
    }
</style>