<!--变电站table-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table row-key="index" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName" height="490" :empty-text="emptyText">
            <el-table-column label="序号" type="index" width="70" />
            <el-table-column label="平台名称" prop="kpName" width="135" show-overflow-tooltip></el-table-column>

            <el-table-column label="起码" prop="sMa" width="100" show-overflow-tooltip></el-table-column>
            <el-table-column label="止码" prop="eMa" width="100" show-overflow-tooltip></el-table-column>
            <el-table-column label="倍率" prop="dbRate" width="70" show-overflow-tooltip></el-table-column>
            <el-table-column label="电量" prop="eptp" width="100" sortable show-overflow-tooltip></el-table-column>
            <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip></el-table-column>
            <el-table-column label="结算起止时间" prop="st" width="320">
                <template slot-scope="scope">
                    <span v-if="scope.row['st']">{{changeTime(scope.row['st'])}}-{{changeTime(scope.row['et'])}}</span>
                </template>
            </el-table-column>
            <el-table-column label="公司名称" prop="kcName" width="230" show-overflow-tooltip></el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination layout="total" :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import Table from "@/components/ElTableCommon.vue";
    import {
        parseTime
    } from "@/utils/index";
    export default {
        extends: Table,
        props: {
            emptyText: {
                type: String,
                default: '暂无数据'
            }
        },
        data() {
            return {}
        },
        methods: {
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}:{s}')
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        cursor: pointer;
        display: flex;
        padding: 0 10px;
        justify-content: space-between;
        align-items: center;

        .edit-btn:hover {
            color: #2E4AF7
        }

        .delete-btn:hover {
            color: #E6A23C
        }

        .center {
            width: 100%;
        }

        .btn {
            color: #2E4AF7;
            text-decoration: underline
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -15px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }

    .avatar {
        display: flex;
        align-items: center;

        img {
            width: 25px;
            height: 25px;
        }
    }
</style>