<!--变电站table-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table row-key="index" :data="tableData" border :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName" height="490" :empty-text="emptyText" :span-method="arraySpanMethod">
            <el-table-column label="序号" prop="index" width="70" fixed="left" />

            <el-table-column v-for="th in heads" :label="th.label" :key="th.prop" :prop="th.prop" :min-width="th.width"
                :show-overflow-tooltip="th.overflow" :sortable="th.sortable" :fixed="th.fixed?th.fixed:false">
                <template slot-scope="scope">
                    {{getText(scope.row,th)}}
                </template>
            </el-table-column>

        </el-table>
        <div class="page pagination-box">
            <el-pagination layout="total" :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import Table from "@/components/ElTableCommon.vue";
    import {
        parseTime
    } from "@/utils/index";
    import {
        getDefaultTimeRange,
        getTimeRangeArr
    } from "@/utils/index.js"
    export default {
        extends: Table,
        props: {
            show: { //显示状态
                type: Boolean,
                default: false
            },
            search: { //搜索条件
                type: Object,
                default: {}
            },
            emptyText: {
                type: String,
                default: '暂无数据'
            }
        },
        data() {
            return {
                commonHeads: [{ //公共表头
                    label: '平台名称',
                    prop: 'kpName',
                    width: 135,
                    overflow: true,
                    fixed: 'left',
                }, {
                    label: '计量点名称',
                    prop: 'jpName',
                    width: 250,
                    overflow: true,
                    fixed: 'left',
                }, {
                    label: '公司名称名称',
                    prop: 'kcName',
                    width: 230,
                    overflow: true,
                }, {
                    label: '倍率',
                    prop: 'dbRate',
                    width: 70,
                }, {
                    label: '项目',
                    prop: 'type',
                    width: 70,
                }],
                heads: [], //表头数组
                data: [], //表格数据
            }
        },
        mounted() {
            //处理默认表头
            const defaultTime = getDefaultTimeRange();
            this._getHeads(defaultTime);
        },
        methods: {
            tableRowClassName({
                row,
                rowIndex
            }) {
                if (rowIndex % 2 == 1) {
                    return 'gray';
                }
                return '';
            },
            arraySpanMethod({
                row,
                column,
                rowIndex,
                columnIndex
            }) {
                if (columnIndex <= 4) {
                    if (rowIndex % 2 === 0) {
                        return {
                            rowspan: 2,
                            colspan: 1
                        };
                    } else {
                        return {
                            rowspan: 0,
                            colspan: 0
                        };
                    }
                }
            },
            getText(row, item) {
                let curr = row[item.prop];
                if (!curr) return '';
                if (item.prop.startsWith('2')) {
                    if (row.type == '电量') {
                        return curr['eptp'];
                    } else if (row.type == '底码') {
                        return curr['ma'];
                    }
                }
                return curr;
            },
            _getHeads(date) {
                const timeArr = getTimeRangeArr(date);
                this.heads = this.commonHeads.concat(timeArr.map(time => {
                    return {
                        label: parseTime(time, '{m}月{d}日'),
                        prop: parseTime(time, '{y}-{m}-{d}'),
                        width: 90,
                    }
                }))
            },
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}:{s}')
            },
        },
        watch: {
            search: {
                handler(val) {
                    this._getHeads([new Date(val.st), new Date(val.et)])
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        cursor: pointer;
        display: flex;
        padding: 0 10px;
        justify-content: space-between;
        align-items: center;

        .edit-btn:hover {
            color: #2E4AF7
        }

        .delete-btn:hover {
            color: #E6A23C
        }

        .center {
            width: 100%;
        }

        .btn {
            color: #2E4AF7;
            text-decoration: underline
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -15px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }

    .avatar {
        display: flex;
        align-items: center;

        img {
            width: 25px;
            height: 25px;
        }
    }

    /deep/ .gray {
        background-color: rgba(0, 0, 0, 0.03);
    }

    /deep/.el-table td {
        padding: 4px 0;
    }
</style>