<template>
    <div class="search" @keyup.enter="search">
        <el-row :gutter="60">
            <el-col :span="9" class="item">
                <div class="label"><span class="important">*</span>选择时间：</div>
                <div class="rt">
                    <el-date-picker v-model="formData.timeArr" format="" type="datetimerange" range-separator="~"
                        start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerBeginDateBefore">
                    </el-date-picker>
                </div>
            </el-col>
            <el-col :span="6" class="item">
                <div class="label">所属公司：</div>
                <div class="rt">
                    <el-select v-model="formData.kcList" size="small" @change="changeKc" multiple filterable remote
                        placeholder="请输入公司名称" :remote-method="remoteMethodKc" :loading="loading">
                        <el-option v-for="item in optionsKcLists" :key="item.code" :label="item.shortName"
                            :value="JSON.stringify(item)">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            <el-col :span="6" class="item">
                <div class="label">所属平台：</div>
                <div class="rt">
                    <el-select v-model="formData.kpList" size="small" @change="changeKp" multiple filterable remote
                        placeholder="请输入平台名称" :remote-method="remoteMethodKp" :loading="loading">
                        <el-option v-for="item in optionsKpLists" :key="item.code" :label="item.name"
                            :value="JSON.stringify(item)">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            <el-col :span="3" class="item">
                <div class="btns">
                    <el-button type="primary" @click="search">查 询</el-button>
                    <el-button @click="reset">重 置</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="60">
            <el-col :span="9" class="item">
                <div class="label">计量点：</div>
                <div class="rt">
                    <el-select v-model="formData.jpList" size="small" multiple filterable remote placeholder="请输入计量点名称"
                        :remote-method="remoteMethodPoint" :loading="loading">
                        <el-option v-for="item in optionsPoints" :key="item.code" :label="item.name"
                            :value="JSON.stringify(item)">
                        </el-option>
                    </el-select>
                </div>
            </el-col>
            <el-col :span="6" class="item">
                <div class="label">户号：</div>
                <div class="rt">
                    <el-select v-model="formData.acList" size="small" multiple filterable remote placeholder="请输入户号"
                        :remote-method="remoteMethodAccount" :loading="loading">
                        <el-option v-for="item in optionsAccounts" :key="item.gcAccount"
                            :label="item.gcAccount + '【' +item.payer+ '】'" :value="item.gcAccount">
                        </el-option>
                    </el-select>

                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    //接口
    import {
        getReportMa,
        getPointsList,
        getKcList,
        getKpList,
        getGcAccQuery
    } from "@/api/archives.js";
    import {
        getDayStart,
        getDefaultTimeRange,
        parseTime
    } from "@/utils/index.js"
    export default {
        props: {},
        data() {
            return {
                formData: {
                    timeArr: [],
                    kcList: [], //选择的公司【对象】数组
                    kpList: [], //选择的平台【对象】数组
                    jpList: [], //选择的计量点【code】数组
                    acList: [], //选择的户号【code】数组

                },
                optionsKcLists: [], //公司列表
                optionsKpLists: [], //平台列表
                optionsPoints: [], //计量点列表
                optionsAccounts: [], //户号列表

                loading: false,
                pickerBeginDateBefore: {
                    disabledDate(time) {
                        return time.getTime() >= (Date.now());
                    }
                },
            }
        },
        mounted() {
            this.formData.timeArr = getDefaultTimeRange();
            this.remoteMethodKc('');
        },
        methods: {
            remoteMethodKc(keyWord) { // 公司搜索
                this.loading = true;
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    _loading: false, //不显示加载中图标
                }
                if (keyWord !== '') {
                    opt.keyWord = keyWord;
                }
                getKcList(opt).then(res => {
                    this.loading = false;
                    if (res.data) {
                        this.optionsKcLists = res.data;
                    } else {
                        this.optionsKcLists = [];
                    }
                })
            },
            remoteMethodKp(keyWord) { // 平台搜索
                this.loading = true;
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    kcCodes: this.formData.kcList.map(_ => JSON.parse(_).code).toString(), //公司code
                    _loading: false, //不显示加载中图标
                }
                if (keyWord !== '') {
                    opt.keyWord = keyWord;
                }
                if (opt.kcCodes == '') {
                    delete opt.kcCodes;
                }
                getKpList(opt).then(res => {
                    this.loading = false;
                    if (res.data) {
                        this.optionsKpLists = res.data;
                    } else {
                        this.optionsKpLists = [];
                    }
                })
            },
            remoteMethodPoint(keyWord) { // 计量点搜索
                this.loading = true;
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    kcCodes: this.formData.kcList.map(_ => JSON.parse(_).code).toString(),
                    kpCodes: this.formData.kpList.map(_ => JSON.parse(_).code).toString(),
                    _loading: false, //不显示加载中图标
                }
                if (keyWord !== '') {
                    opt.jpName = keyWord;
                }
                if (opt.kcCodes == '') {
                    delete opt.kcCodes;
                }
                if (opt.kpCodes == '') {
                    delete opt.kpCodes;
                } else {
                    delete opt.kcCodes;
                }
                getPointsList(opt).then(res => {
                    this.loading = false;
                    if (res.list && res.list.rows) {
                        this.optionsPoints = res.list.rows;
                    } else {
                        this.optionsPoints = [];
                    }
                })
            },
            //户号查询
            remoteMethodAccount(keyWord) {
                this.loading = true;
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    _loading: false, //不显示加载中图标
                }
                if (keyWord !== '') {
                    opt.gcAcc = keyWord;
                }
                getGcAccQuery(opt).then(res => {
                    this.loading = false;
                    if (res.data) {
                        this.optionsAccounts = res.data;
                    } else {
                        this.optionsAccounts = [];
                    }
                })
            },
            //选择公司
            changeKc() {
                this.remoteMethodKp('');
                //过滤已经选择的平台
                this.formData.kpList = this.formData.kpList.filter(item =>
                    this.formData.kcList.find(kc => JSON.parse(kc).code == JSON.parse(item).kcCode));
                //过滤已经选择的计量点
                this.formData.jpList = this.formData.jpList.filter(item =>
                    this.formData.kcList.find(kc => JSON.parse(kc).code == JSON.parse(item).kcCode));
            },
            //选择平台
            changeKp() {
                this.remoteMethodPoint('');
                //过滤已经选择的计量点
                this.formData.jpList = this.formData.jpList.filter(item =>
                    this.formData.kpList.find(kp => JSON.parse(kp).code == JSON.parse(item).kpCode));
            },
            search(notice = true) {
                if (notice && this.formData.timeArr.toString() == '') {
                    this.$message.warning('请选择时间');
                    return;
                }
                let opt = {
                    st: parseTime(this.formData.timeArr[0]),
                    et: parseTime(this.formData.timeArr[1]),
                    kcCodes: this.formData.kcList.map(_ => JSON.parse(_).code).toString(),
                    kpCodes: this.formData.kpList.map(_ => JSON.parse(_).code).toString(),
                    jpCodes: this.formData.jpList.map(_ => JSON.parse(_).code).toString(),
                    gcAccounts: this.formData.acList.toString(),
                }
                if (opt.kcCodes == '') delete opt.kcCodes;
                if (opt.kpCodes == '') delete opt.kpCodes;
                if (opt.jpCodes == '') delete opt.jpCodes;
                if (opt.gcAccounts == '') delete opt.gcAccounts;

                if (this.formData.jpList.length) {
                    delete opt.kpCodes;
                    delete opt.kcCodes;
                }
                if (this.formData.kpList.length) {
                    delete opt.kcCodes;
                }
                this.$emit('searchHand', opt)
            },
            reset() {
                this.formData.timeArr = [];
                this.formData.kcList = [];
                this.formData.kpList = [];
                this.formData.jpList = [];
                this.formData.acList = [];
                this.$emit('searchHand', 'reset')
            }
        }
    }
</script>
<style scoped lang="scss">
    .search {
        padding: 16px 0 0px 0;

        /deep/ .el-range-editor,
        /deep/ .el-select {
            width: 100%;
        }

        .item {
            margin-bottom: 16px;
            display: flex;
            line-height: 32px;
            height: 32;

            /deep/ .el-input__inner {
                // width: 270px;
                flex: 1;
            }

            .label {
                min-width: 80px;
                margin-right: 2px;
                text-align: left;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;
            }
        }


        .btns {
            width: 100%;
            display: flex;
            justify-content: flex-end;
        }
    }
</style>